"use strict";

import { tns } from "tiny-slider/src/tiny-slider";
//import { tns } from "../../../../node_modules/tiny-slider/src/tiny-slider";
import { placeLineX, setCounter } from "../../../js/import/functions";

if (document.querySelector(".rest-conf")) {
  if (document.querySelector(".rest-conf-slider--olimp")) {
    const rest_conf_slider_olimp = tns({
      container: ".rest-conf-slider--olimp",
      items: 1,
      slideBy: "page",
      autoplay: true,
      touch: true,
      mouseDrag: true,
      loop: false,
      rewind: true,
      prevButton: ".rest-conf .slider__prev--olimp", 
      nextButton: ".rest-conf .slider__next--olimp",
      controlsContainer: ".rest-conf-slider-controls--olimp",
    });
    setCounter('rest-conf', rest_conf_slider_olimp, 'rest-conf-olimp');

    const rest_conf_slider_iliada = tns({
      container: ".rest-conf-slider--iliada",
      items: 1,
      slideBy: "page",
      autoplay: true,
      touch: true,
      mouseDrag: true,
      loop: false,
      rewind: true,
      prevButton: ".rest-conf .slider__prev--iliada", 
      nextButton: ".rest-conf .slider__next--iliada",
      controlsContainer: ".rest-conf-slider-controls--iliada",
    });
    setCounter('rest-conf', rest_conf_slider_iliada, 'rest-conf-iliada');

    const rest_conf_slider_germes = tns({
      container: ".rest-conf-slider--germes",
      items: 1,
      slideBy: "page",
      autoplay: true,
      touch: true,
      mouseDrag: true,
      loop: false,
      rewind: true,
      prevButton: ".rest-conf .slider__prev--germes", 
      nextButton: ".rest-conf .slider__next--germes",
      controlsContainer: ".rest-conf-slider-controls--germes",
    });
    setCounter('rest-conf', rest_conf_slider_germes, 'rest-conf-germes');
  }
  if (document.querySelector(".rest-conf-slider--paradise")) {
    const rest_conf_slider_paradise = tns({
      container: ".rest-conf-slider--paradise",
      items: 1,
      slideBy: "page",
      autoplay: true,
      touch: true,
      mouseDrag: true,
      loop: false,
      rewind: true,
      prevButton: ".rest-conf .slider__prev--paradise", 
      nextButton: ".rest-conf .slider__next--paradise",
      controlsContainer: ".rest-conf-slider-controls--paradise",
    });
    setCounter('rest-conf', rest_conf_slider_paradise, 'rest-conf-paradise');
  
    const rest_conf_slider_prado = tns({
      container: ".rest-conf-slider--prado",
      items: 1,
      slideBy: "page",
      autoplay: true,
      touch: true,
      mouseDrag: true,
      loop: false,
      rewind: true,
      prevButton: ".rest-conf .slider__prev--prado", 
      nextButton: ".rest-conf .slider__next--prado",
      controlsContainer: ".rest-conf-slider-controls--prado",
    });
    setCounter('rest-conf', rest_conf_slider_prado, 'rest-conf-prado');
    
    const rest_conf_slider_ingreco = tns({
      container: ".rest-conf-slider--ingreco",
      items: 1,
      slideBy: "page",
      autoplay: true,
      touch: true,
      mouseDrag: true,
      loop: false,
      rewind: true,
      prevButton: ".rest-conf .slider__prev--ingreco", 
      nextButton: ".rest-conf .slider__next--ingreco",
      controlsContainer: ".rest-conf-slider-controls--ingreco",
    });
    setCounter('rest-conf', rest_conf_slider_ingreco, 'rest-conf-ingreco');
  
    const rest_conf_slider_weddings = tns({
      container: ".rest-conf-slider--weddings",
      items: 1,
      slideBy: "page",
      autoplay: true,
      touch: true,
      mouseDrag: true,
      loop: false,
      rewind: true,
      prevButton: ".rest-conf .slider__prev--weddings", 
      nextButton: ".rest-conf .slider__next--weddings",
      controlsContainer: ".rest-conf-slider-controls--weddings",
    });
    setCounter('rest-conf', rest_conf_slider_weddings, 'rest-conf-weddings');
  }

  let rest_conf_slider_icon = document.querySelectorAll(".rest-conf .slider__icon");
  rest_conf_slider_icon.forEach(function (icon) {
    icon.addEventListener("click", function (event) {
      event.preventDefault();
      let parent = document.getElementById(icon.dataset.parent);
      let prev_icon = parent.querySelector(".rest-conf .slider__icon--active");
      let rest_conf_slider_item = parent.querySelector(".rest-conf-slider");
      let rest_conf_slider_row = parent.querySelector(".rest-conf-slider-row");
      parent.querySelector(".rest-conf-slider-controls").classList.toggle("rest-conf-slider-controls--module");
      if (rest_conf_slider_item) {
        rest_conf_slider_item.classList.remove("rest-conf-slider");
        rest_conf_slider_item.classList.add("rest-conf-slider-row");
      } else if (rest_conf_slider_row) {
        rest_conf_slider_row.classList.remove("rest-conf-slider-row");
        rest_conf_slider_row.classList.add("rest-conf-slider");
      }
    
      icon.classList.toggle("slider__icon--active");
      prev_icon.classList.toggle("slider__icon--active");
    });
  });

  let rest_conf_items = document.querySelectorAll(".rest-conf-list__item");
  let rest_conf_underline =  document.querySelector('.rest-conf .underline');
  let rest_conf_item_active = document.querySelector(".rest-conf-list__item--active");
  placeLineX(rest_conf_underline, {'width': rest_conf_item_active.clientWidth + 'px','left': rest_conf_item_active.offsetLeft + 'px',});

  rest_conf_items.forEach(function (link) {
    link.addEventListener("click", function (event) {
      placeLineX(rest_conf_underline, {'width': event.target.clientWidth + 'px', 'left': event.target.offsetLeft + 'px',});
      event.preventDefault();
      let prev_item = document.querySelector(".rest-conf-preview-list__item--active");
      let prev_link = document.querySelector(".rest-conf-list__item--active");
      let item = document.querySelector(link.getAttribute("href"));

      item.classList.toggle("rest-conf-preview-list__item--active");
      prev_item.classList.toggle("rest-conf-preview-list__item--active");
      prev_link.classList.toggle("rest-conf-list__item--active");
      link.classList.toggle("rest-conf-list__item--active");
    });
  });
}